.container {
    width: 100%;
    height: 20vh;
    background-color: var(--Tag-Color);
    display: flex;
    justify-content: center;
    align-items: center;
}

.content {
    display: flex;
    width: fit-content;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.content .contact {
    display: flex;
    width: fit-content;
    align-items: center;
    justify-content: center;
}

@media screen and (min-width: 1000px) {
    .content .contact img {
        width: 40px;
    }

    .content .contact a:not(:last-child) {
        margin-right: 30%;
    }

    .content .copyright {
        margin-top: 10%;
        font-size: 0.9em;
        color: var(--PaperWhite-color);
    }
}

@media screen and (max-width: 1000px) {
    .content .contact img {
        width: 30px;
    }

    .content .contact a:not(:last-child) {
        margin-right: 30%;
    }

    .content .copyright {
        margin-top: 10%;
        font-size: 0.8em;
        color: var(--PaperWhite-color);
    }
}